import React from "react";
import DataGallery from "../Data";
import "./data.css";

function Gallry() {
  var fatchData = DataGallery.map((data) => {
    return (
      <>
        <div className="col-md-4">
          <div className="box">
            <div className="img-box">
              <img src={data.image} alt="" />
            </div>
            <div className="text-box">
              <h3>{data.title}</h3>
              <p>{data.description}</p>

              <div className="d-flex justify-content-around ">
                <span>{data.price} $ </span>
                <a href="#" className="btn text-white btn-shop">
                  Shop Now
                </a>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  });

  return (
    <section className="galry">
      <div className="container">
        <div className="row text-center">{fatchData}</div>
      </div>
    </section>
  );
}

export default Gallry;
